#root,
#root>div {
  width: 100%;
  height: 100%;
}

.root {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  flex-direction: column
}

.root-header.ant-layout-header {
   padding: 0;
   -webkit-app-region: drag
}
.root-header-body{
  width: 100%;
  height: 100%;
}


.root-body {
  /* position: absolute;
  top: 3.75rem;
  bottom: 6.25rem;
  left: 0;
  right: 0; */
  min-width: 51.25rem;
  max-width: 56.25rem;
  margin: auto;
  min-height: 100%;
  /* display: -webkit-flex;
  display: flex;
  flex: 1 */
}

.root-body .nav {
  width: 18.75rem;
  border-right: 1px solid #a40011;
}

.root-body .container {
  /* -webkit-flex: 1;
  flex: 1; */
  min-height: 100%;
}

.root-footer {
  /* position: absolute;
  bottom: 0;
  left: 0;
  right: 0; */
  flex: 0 0 auto;
  height: 3.75rem;
  box-sizing: border-box;
  background-color: rgb(49, 49, 49);

  /* border-top: 1px solid #a40011; */
  color: #fff
}


